*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

body {
  margin: 0;
  display: grid;
  min-width: 320px;
  min-height: 100vh;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Open Sans", "Arial, sans-serif"; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.page-header {
  position: absolute;
  width: 100%;
  padding-top: 21px; }

.page-header__wrapper {
  max-width: 1166px;
  width: 100%;
  margin: 0 auto; }

.page-header__toggle {
  display: none;
  width: 50px;
  height: 50px;
  padding: 0;
  border: none;
  background-color: #0ad9c6;
  background-image: url("../../img/menu-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer; }

.main-nav {
  display: flex;
  justify-content: center; }

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 874px;
  width: 100%;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  list-style: none; }

.menu__item {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 9px; }

.menu__link {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: #ffffff;
  opacity: 0.7; }
  .menu__link:hover, .menu__link:focus {
    opacity: 1; }

.hero {
  min-height: 825px;
  background-color: #131314;
  background-image: url("../../img/hero.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.hero__wrapper {
  display: flex;
  padding-top: 318px;
  padding-bottom: 240px;
  max-width: 880px;
  margin: 0 auto;
  color: #ffffff; }

.hero__description {
  max-width: 393px; }

.hero__logo {
  margin-bottom: 31px; }

.hero__title {
  margin: 0;
  margin-bottom: 24px;
  font-size: 46px;
  font-weight: 700;
  line-height: 64px;
  letter-spacing: -0.005em; }

.hero__subtitle {
  font-size: 20px;
  font-weight: 300;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: rgba(255, 255, 255, 0.8); }

.hero__form {
  max-width: 327px;
  margin-left: auto; }

.hero__form-title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em; }

.form__input {
  display: block;
  width: 100%;
  margin-bottom: 20px;
  padding-top: 14px;
  padding-bottom: 10px;
  padding-left: 30px;
  border: 1px solid #c9ccd4;
  border-radius: 6px;
  font: inherit;
  font-size: 15px;
  line-height: 24px; }

.form__submit {
  width: 100%;
  padding: 15px;
  font: inherit;
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  background-color: #0ad9c6;
  cursor: pointer; }
